.Log {
  width: 75%;
  background: #333;
  -ms-flex-positive: 2;
      flex-grow: 2;
  margin: 10px 0 20px 0;
  height: 100px;
  overflow-y: scroll;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.Entry {
  height: 100px;
  padding: 10px 30px;
  /* border: 1px solid blue; */
}

.Log::-webkit-scrollbar {
  width: 0.5em;
  height: 0.09em;
  background-color: #eee;
}

.Log::-webkit-scrollbar-thumb {
  background-color: #2D91F2;
  border-radius: 3px;
}



